*,
*::before,
*::after {
    box-sizing: border-box;
}

body {
    margin: 0 auto;
    max-width: 1280px;
    color: $primary-text-color;
}

.container {
    max-width: 414px;
    margin: 0 auto;
    padding: 0 15px;

    @media screen and (min-width: $tablet) {
        max-width: 1024px;
        padding: 0 47px;
    }

    @media screen and (min-width: $desktop) {
        max-width: 1280px;
        padding: 0 85px;
    }
}

.social-network-icon {
    fill: $primary-color;

    transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);

    &:hover,
    :focus {
        fill: $accent-color;
    }
}

.last-letter {
    letter-spacing: 0;
}
