ul {
    margin: 0px;
    list-style-type: none;
    padding-left: 0;
}

a {
    text-decoration: none;
}

img {
    display: block;
    width: 100%;
    height: auto;
}

h1,
h2,
h3,
ul,
h4,
h5,
h6,
p {
    margin: 0 0;
}

textarea,
select,
input,
button {
    outline: none;
}

select {
    appearance: none;
}

img {
    display: block;
    height: auto;
}